import Header from './components/Header';
import Tasks from './components/Tasks';
import { useState } from 'react';
import AddTask from './components/AddTask';



function App() {
   const [showAddTask, setShowAddTask] = useState(false)
   const [tasks, setTasks] = useState([
  {
    id: 1,
    text: 'Doctors Appointment',
    day: 'Fev 5th at 2:30pm',
    reminder: true,
  },
  {
    id: 2,
    text: 'Collect samples from Lab',
    day: 'Jan 6th at 11:00am',
    reminder: true,
  },
  {
    id: 3,
    text: 'Complete the assessment',
    day: 'May 20th at 3:00pm',
    reminder: true,

   }
]);
  const addTask = (task) => {
      const id = Math.floor(Math.random()*10000) + 1
     setTasks([...tasks,{id, ...task}])
  }

  
  const deleteTask = (id) => {
     setTasks(tasks.filter((task)=>task.id !== id))
  }

  //toogle reminder
  const toggleReminder = (id) => {
    setTasks(tasks.map((task)=>tasks.id===id? {...task, reminder:!task.reminder}: task))
  }

  return (
    <div className="container">
      <Header showAdd={showAddTask} onAdd={()=>setShowAddTask(!showAddTask)} title='Task Tracker'></Header>
      {showAddTask && <AddTask onAdd={addTask}></AddTask>}
      {tasks.length > 0 ?<Tasks tasks={tasks} onToggle={toggleReminder} onDelete={deleteTask}></Tasks>:' No taks to show'}
    </div>
  );
}

export default App;
